import { useCallback, useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { Fn } from "../../../../../helper/function";
import { PaginationRequest, TableColumn } from "../../../../../models/baseModel";
import { WorkflowDetailService } from "../../../../../services/taskService";
import { MyBox } from "../../../../core/MyBox";
import { OrderByModel, PageInfoModel } from "../../../../core/TableModel";
import TableComponent from "../../../../core/TableNewComponent";
import { UpOutlined, DownOutlined } from "@ant-design/icons";
import { MyButton } from "../../../../core/MyButton";
import { Tag } from "antd";
import ModifyDetailComponent from "./ModifyDetailComponent";

export default function DetailCompont() {
    const projectKey = useParams().projectKey;
    const workflowKey = useParams().workflowKey;

    const column: any = [
        new TableColumn({ title: "STT", key: "stt", render: (data: any, row: any, index: number) => Fn.RenderSTT(data, row, index, pageInfo) }),
        new TableColumn({
            title: "Api name", key: "documentApi", render: (data: any, row: any) =>
                <MyButton onClick={() => handleDoubleClickDetail(row.key)} value={data.name} />
        }),
        new TableColumn({ title: "Thời gian trễ", key: "timeDelay" }),
        new TableColumn({ title: "Kết quả pass", key: "resultPass", render: (data: boolean) => <Tag color={data ? "green" : "red"}>{data ? "True" : "False"}</Tag> }),
        new TableColumn({ title: "Status code pass", key: "statusCodePass" }),
        new TableColumn({ title: "Response pass", key: "responsesPass" }),
        new TableColumn({ title: "Lặp lại", key: "repeat" }),
        new TableColumn({
            title: "Thứ tự", key: "orders", render: (data: any, row: any) =>
                <span>
                    <MyButton onClick={() => handleChangeOrder(row.key, false)} type="link" value={<UpOutlined />} />
                    - {data} -
                    <MyButton onClick={() => handleChangeOrder(row.key, true)} type="link" value={<DownOutlined />} />
                </span>
        }),
    ]
    const [pageInfo, setPageInfo] = useState<PageInfoModel>(new PageInfoModel());
    const [orderBy, setOrderBy] = useState<OrderByModel>(new OrderByModel());
    const [data, setData] = useState([]);
    const [isLoading, setIsLoading] = useState(false);

    const [visible, setVisible] = useState(false);
    const [visibleEdit, setVisibleEdit] = useState(false);
    const [keyEdit, setKeyEdit] = useState<string>();

    const loadData = useCallback(() => {
        setIsLoading(true);
        let paginationRequest: PaginationRequest = new PaginationRequest(pageInfo.pageNumber, pageInfo.pageSize, orderBy);
        WorkflowDetailService.GetList(paginationRequest, projectKey, workflowKey)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    setPageInfo(new PageInfoModel(rs));
                    setData(rs.data.pagedData);
                }
            })
            .finally(() => setIsLoading(false))
    }, [pageInfo.pageNumber, pageInfo.pageSize, orderBy, projectKey, workflowKey])

    function handleChangeOrder(key: string, isNext: boolean) {
        setIsLoading(true);
        WorkflowDetailService.SwapOrder(key, projectKey, workflowKey, isNext)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    loadData();
                }
            })
            .finally(() => setIsLoading(false))
    }

    useEffect(() => {
        loadData();
    }, [loadData])

    function handleClickAdd() {
        setVisible(true);
    }

    function handleDoubleClickDetail(key: string) {
        setKeyEdit(key);
        setVisibleEdit(true);
    }

    return (
        <MyBox
            title="Chi tiết workflow"
            buttonList={() => <MyButton type="primary" onClick={handleClickAdd} value="Thêm mới" />}>
            <TableComponent
                pageInfo={pageInfo}
                setPageInfo={setPageInfo}
                orderBy={orderBy}
                setOrderBy={setOrderBy}
                column={column}
                dataSource={data}
                loading={isLoading}
                loadData={loadData} />

            <ModifyDetailComponent
                projectKey={projectKey}
                workflowKey={workflowKey}
                visible={visible}
                setVisible={setVisible}
                isLoading={isLoading}
                setIsLoading={setIsLoading}
                loadData={loadData} />

            <ModifyDetailComponent
                projectKey={projectKey}
                workflowKey={workflowKey}
                visible={visibleEdit}
                setVisible={setVisibleEdit}
                keyEdit={keyEdit}
                setKeyEdit={setKeyEdit}
                isLoading={isLoading}
                setIsLoading={setIsLoading}
                loadData={loadData} />
        </MyBox>
    );
}