import { Col, Drawer, Form, Row } from "antd";
import { useCallback, useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Swal from "sweetalert2";
import { Fn } from "../../../../../helper/function";
import { ApiDocService, WorkflowDetailService } from "../../../../../services/taskService";
import { MyFormItem } from "../../../../core/Input";
import { MyButton } from "../../../../core/MyButton";
import SearchComponent from "../../../../core/SearchComponent";

export default function ModifyDetailComponent(props: any) {
    const { projectKey, workflowKey, visible, setVisible, isLoading, setIsLoading, keyEdit, setKeyEdit, loadData } = props;

    const [dataSearch, setDataSearch] = useState([]);
    const [apiKey, setApiKey] = useState();
    const [apiName, setApiName] = useState();

    const [api, setApi] = useState<any>();
    const [timeDelay, setTimeDelay] = useState(0);
    const [resultPass, setResultPass] = useState(true);
    const [statusCodePass, setStatusCodePass] = useState(0);
    const [responsesPass, setResponsePass] = useState();
    const [repeat, setRepeat] = useState(0);

    function handleSearchApi(val: string) {
        setIsLoading(true);
        ApiDocService.Query(projectKey, val)
            .then((rs: any) => {
                setDataSearch(rs.data);
            })
            .finally(() => setIsLoading(false))
    }

    const loadDataDetail = useCallback(() => {
        if (keyEdit !== undefined) {
            setIsLoading(true);
            WorkflowDetailService.GetDetail(projectKey, workflowKey, keyEdit)
                .then((rs: any) => {
                    if (rs.isSuccess) {
                        console.log(rs.data);
                        setApi(rs.data.documentApi);
                        setApiKey(rs.data.documentApi.key);
                        setApiName(rs.data.documentApi.name);
                        setTimeDelay(rs.data.timeDelay);
                        setResultPass(rs.data.resultPass);
                        setStatusCodePass(rs.data.statusCodePass);
                        setResponsePass(rs.data.responsesPass);
                        setRepeat(rs.data.repeat);
                    }
                })
                .finally(() => setIsLoading(false))
        }
    }, [projectKey, workflowKey, keyEdit, setIsLoading])

    useEffect(() => {
        loadDataDetail();
    }, [loadDataDetail])

    function OnClose() {
        setVisible(false);
        if (setKeyEdit !== undefined) {
            setKeyEdit(undefined);
        }
    }

    function handleClickDelete() {
        Swal.fire({
            title: "Xác nhận xóa",
            text: "Bạn xác nhận xóa api này không?"
        })
            .then((rs) => {
                if (rs.isConfirmed) {
                    doDelete();
                }
            })
    }

    function doDelete() {
        setIsLoading(true);
        WorkflowDetailService.GetDetail(projectKey, workflowKey, keyEdit)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    WorkflowDetailService.Delete(projectKey, workflowKey, keyEdit, Fn.GetTimestamp(rs))
                        .then((rs: any) => {
                            if (rs.isSuccess) {
                                Fn.PrintMsgSuccess(rs);
                                OnClose();
                                loadData();
                            } else setIsLoading(false);
                        }).finally(() => setIsLoading(false))
                } else setIsLoading(false);
            })
    }

    function handleSubmitForm() {
        var body = {
            documentApiKey: apiKey,
            timeDelay: timeDelay,
            resultPass: resultPass,
            statusCodePass: statusCodePass,
            responsesPass: responsesPass,
            repeat: repeat
        };

        setIsLoading(true);
        if (keyEdit !== undefined) {
            edit(body);
        } else {
            add(body);
        }
    }

    function add(body: any) {
        WorkflowDetailService.Add(projectKey, workflowKey, body)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    Fn.PrintMsgSuccess(rs);
                    loadData();
                } else setIsLoading(false);
            })
    }

    function edit(body: any) {
        WorkflowDetailService.GetDetail(projectKey, workflowKey, keyEdit)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    body = Fn.GetTimestamp(rs, body);
                    WorkflowDetailService.Edit(projectKey, workflowKey, keyEdit, body)
                        .then((rs: any) => {
                            if (rs.isSuccess) {
                                Fn.PrintMsgSuccess(rs);
                                loadData();
                            } else setIsLoading(false);
                        }).finally(() => setIsLoading(false))
                } else setIsLoading(false);
            })
    }

    return (
        <Drawer
            title={keyEdit !== undefined ? "Chỉnh sửa" : "Thêm"}
            open={visible}
            onClose={OnClose}>
            <Form
                layout="vertical"
                onSubmitCapture={handleSubmitForm}>
                <SearchComponent
                    label="Api name"
                    onSearch={handleSearchApi}
                    dataSearch={dataSearch}
                    isLoading={isLoading}
                    resultKey={apiKey}
                    setResultKey={setApiKey}
                    resultName={apiName}
                    setResultName={setApiName} />

                <MyFormItem.Number
                    label="Thời gian trễ"
                    min={0}
                    value={timeDelay}
                    setValue={setTimeDelay} />

                <MyFormItem.Select
                    label="Kết quả pass"
                    options={[
                        { label: "True", value: true },
                        { label: "False", value: false }
                    ]}
                    value={resultPass}
                    setValue={setResultPass} />

                <MyFormItem.Number
                    label="Status code pass"
                    min={0}
                    value={statusCodePass}
                    setValue={setStatusCodePass} />

                <MyFormItem.TextArea
                    label="Response pass"
                    value={responsesPass}
                    setValue={setResponsePass} />

                <MyFormItem.Number
                    label="Lặp lại"
                    min={0}
                    value={repeat}
                    setValue={setRepeat} />

                <MyButton
                    type="success"
                    htmlType="submit"
                    value={keyEdit !== undefined ? "Chỉnh sửa" : "Thêm"} />

                <MyButton
                    type="danger"
                    onClick={handleClickDelete}
                    value="Xóa" />
            </Form>

            <Row>
                <Col span={24}>
                    <Link to={`/tools/tasks/projects/${projectKey}/codeManage/document/${api?.folder.key}/apis/${api?.key}`}>Chi tiết API</Link>
                </Col>
            </Row>
        </Drawer>
    );
}